@import url('https://fonts.googleapis.com/css2?family=Lemon&family=Onest&family=Open+Sans&display=swap');

*{
   margin: 0;
   padding: 0;
}
body{

   background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
   font-family: 'Lemon', serif;
   display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}
header{
   align-items: center;
   color: black;
}
.app{
   background: rgb(23,204,68);
   background: linear-gradient(118deg, rgba(23,204,68,0.9417016806722689) 17%, rgba(36,107,113,0.8128501400560224) 61%);
   width: 95%;
   min-height: 200px;
   height: min-content;
   border-radius: 15px;
   padding: 20px;
   box-shadow: 10px 10px 42px 0 black;
    display:flex;
   justify-content: space-evenly;
   }
   .score-section{
      display: flex;
      align-items: center;
      font-size: 30px;
      color:rgb(16, 45, 7);
      font-weight: bold;
   }
.question-section{
 width: 100%;
   position: relative;

}
.question-count{
   margin-bottom: 20px;
   font-size: 20px;
}
.question-count span{

   font-size: 30px;
}
.question-text{
   color: white;
   font-size: 30px;
   font-weight: bold;
}
.answer-section{
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 100%;
   justify-content: space-between;
}
button{
   width: 100%;
   color: black;
   font-size: 16px;
   background: linear-gradient(118deg, rgba(23,204,68,0.9417016806722689) 17%, rgba(36,107,113,0.8128501400560224) 61%);
   border-radius: 15px;
   padding: 10px;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   border: 2px solid red;
   cursor: pointer;

}
   